import React, { Component } from 'react';

import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import Button from '@mui/material/Button';
import Grid from '@mui/material/Grid';
import GitHubIcon from '@mui/icons-material/GitHub';

import CardMedia from '@mui/material/CardMedia';
import { CardActions, Typography } from '@mui/material';

export default class Porfolio extends Component {
  render() {
    let resumeData = this.props.resumeData;
    return (
      <section id="portfolio">
        <div className="row">
          <div className="twelve columns collapsed">
            <div className='row item'>

            </div>
            <div id="portfolio-wrapper" className="bgrid-thirds s-bgrid-halves cf">
              {
                resumeData.portfolio && resumeData.portfolio.map((item) => {
                  return (
                    <div className="columns portfolio-item">
                      <div className="item-wrap">
                        <a href="#modal-01">
                          <div className="overlay">
                            <div className="portfolio-item-meta">
                              <h5>{item.name}</h5>
                              <p>{item.description}</p>
                            </div>
                          </div>
                        </a>
                        <Card className='item' sx={{ maxWidth: 330, height: 400 }}>
                          <CardMedia sx={{ height: 150 }}
                            component="img"
                            alt={item.name}
                            height="140"
                            image={item.imgurl}
                          />
                          <CardContent>
                            <h3>{item.name}</h3>
                            <Typography gutterBottom className="card-text">
                              {item.description}
                            </Typography>
                          </CardContent>
                          <CardActions>
                            {
                              item.githuburl &&
                              <Button className="github-button" variant="outlined" startIcon={<GitHubIcon />} href={item.githuburl}>
                                View on Github
                              </Button>
                            }
                          </CardActions>
                        </Card>
                      </div>
                    </div>
                  )
                })
              }
            </div>
          </div>
        </div>
      </section>
    );
  }
}